<template>
  <AppLayout>
    <ViewWrapper
      title="folders"
      :breadcrumbs="breadcrumbs"
      :tabs="tabs"
      :tab="tab"
    >
      <div id="repository-sign-reports">
        <Portal to="action-bar">
          <keep-alive>
            <ItemActions
              :id="repositoryId"
              item="repositories-sign-report"
              :columns="[]"
              group-by=""
              order=""
              :filter-by="[]"
              criteria=""
              active-view=""
              :hide-actions="[
                'toggle',
                'sort',
                'group',
                'filter',
                'activeView',
              ]"
              :simple-search-filter="true"
              :refresh="false"
              :export="false"
              :export-data="exportDataList"
              @refresh="refreshData"
              @search="searchRow"
              @export="export_Data"
              @filter="getData"
            />
          </keep-alive>
        </Portal>

        <SimpleFilter
          :id.sync="repositoryId"
          :workspace-id.sync="workspaceId"
          :columns="_columns"
          :filter-by.sync="filterBy"
          :workspace="true"
          name="Select Folder"
          :workspace-list="workspaceList"
          :list="repositoriesList"
          module="repository"
          :module-id="repositoryId"
          :active-view="activeView"
          @update="getSignReport"
        />

        <BaseLoadingBarSheet v-if="loadingBarContent" class="loading-content" />

        <div class="content">
          <template v-if="repositorySignReportList.length">
            <SignReportList
              :mode="mode"
              :items="items"
              :columns="columns"
              @view="openFile"
              @history="showHistory"
            />
          </template>

          <template v-else-if="!loadingBarContent">
            <StateWrapper
              icon="eva-archive-outline"
              title="Generate Report"
              description="Select workspace and repository to display data"
              style="margin-top: 20px"
            />
          </template>
        </div>

        <Pagination
          v-if="repositorySignReportList.length"
          :total-items="totalItems"
          :current-page.sync="currentPage"
          :items-per-page.sync="itemsPerPage"
          :file-sheet="hideFileSheet"
        />

        <FileSheet
          v-model="isFileSheetVisible"
          :file="selectedFile"
          :repository-field="[]"
          :options="options"
        />
        <ProcessHistory
          v-if="historyProcessId"
          :process-id="historyProcessId"
          :selected-process="selectedProcess"
          @close="historyProcessId = 0"
        />
      </div>
    </ViewWrapper>
  </AppLayout>
</template>

<script>
import AppLayout from "@/layouts/app/AppLayout.vue";
import ViewWrapper from "@/components/common/view-wrapper/ViewWrapper.vue";
import { Portal } from "portal-vue";
import { repository, user, report, workspace } from "@/api/factory.js";
import ItemActions from "@/components/common/display/item-actions/ItemActions.vue";
import SignReportList from "./components/SignatureReportList.vue";
import DataType from "@/constants/data-type.js";
import SimpleFilter from "@/components/common/display/item-actions/components/SimpleFilter.vue";
import StateWrapper from "@/components/common/state/StateWrapper.vue";
import Pagination from "@/components/common/display/Pagination.vue";
import { exportData } from "@/helpers/export-excel-data";
import FileSheet from "../repository-browse-and-trash/components/FileSheet.vue";
import formatBytes from "@/helpers/format-bytes.js";
import ProcessHistory from "@/components/common/workflow/ProcessHistory.vue";

export default {
  name: "RepositoriesSignatureReports",

  components: {
    Portal,
    ItemActions,
    SignReportList,
    SimpleFilter,
    StateWrapper,
    Pagination,
    FileSheet,
    AppLayout,
    ViewWrapper,
    ProcessHistory,
  },

  data() {
    return {
      mode: "REPORT",
      activeView: "TABLE",
      sortBy: {
        criteria: "",
        order: "ASC",
      },
      breadcrumbs: [
        {
          id: this.$nano.id(),
          label: "dashboard",
          route: "dashboard",
        },
        {
          id: this.$nano.id(),
          label: "folders",
          route: "repositories-browse",
        },
        {
          id: this.$nano.id(),
          label: "reports",
          route: "repositories-reports-overview",
        },
        {
          id: this.$nano.id(),
          label: "",
          route: "",
        },
      ],
      tabs: [
        // {
        //   id: this.$nano.id(),
        //   label: "overview",
        //   icon: "mdi-view-dashboard-outline",
        //   route: "repositories-overview",
        //   access: false,
        //   accessLabel: "Overview",
        // },
        {
          id: this.$nano.id(),
          label: "browse",
          icon: "eva-folder-outline",
          route: "repositories-browse",
          access: true,
          accessLabel: "Manage Folders",
        },
        {
          id: this.$nano.id(),
          label: "trash",
          icon: "eva-trash-2-outline",
          route: "repositories-trash",
          access: true,
          accessLabel: "Manage Folders",
        },
        // {
        //   id: this.$nano.id(),
        //   label: "deleted files",
        //   icon: "eva-trash-2-outline",
        //   route: "file-trash",
        //   access: false,
        //   accessLabel: "Manage Folders",
        // },
        {
          id: this.$nano.id(),
          label: "reports",
          icon: "eva-trash-2-outline",
          route: "repositories-signature-report",
          access: false,
          accessLabel: "Audit Report",
        },
      ],
      tab: "repositories-signature-report",
      columns: [
        {
          id: this.$nano.id(),
          name: "fileName",
          label: "File Name",
          type: "TITLE",
          isVisible: true,
          isSortable: true,
          isGroupable: false,
          dataType: DataType.SHORT_TEXT,
        },
        {
          id: this.$nano.id(),
          name: "signType",
          label: "Sign Type",
          isVisible: true,
          isSortable: true,
          isGroupable: true,
          dataType: DataType.SHORT_TEXT,
        },
        {
          id: this.$nano.id(),
          name: "fileStatus",
          label: "Status",
          isVisible: true,
          options: this.fileStatus,
          isSortable: true,
          isGroupable: false,
          dataType: DataType.SHORT_TEXT,
          onClick: this.showHistory,
        },
        {
          id: this.$nano.id(),
          name: "requestedBy",
          label: "Requested By",
          isVisible: true,
          isSortable: true,
          isGroupable: false,
          options: this.userList,
          dataType: DataType.SINGLE_SELECT,
        },
        {
          id: this.$nano.id(),
          name: "currentUser",
          label: "Current User",
          isVisible: true,
          isSortable: true,
          isGroupable: false,
          options: this.userList,
          dataType: DataType.SINGLE_SELECT,
        },
        {
          id: this.$nano.id(),
          name: "requestedAt",
          label: "Requested At",
          isVisible: true,
          isSortable: true,
          isGroupable: true,
          dataType: DataType.DATE,
        },
        // {
        //   id: this.$nano.id(),
        //   name: "Stage",
        //   label: "Stage",
        //   isVisible: true,
        //   isSortable: true,
        //   isGroupable: true,
        //   dataType: DataType.DATE,
        // },
        {
          id: this.$nano.id(),
          name: "version",
          label: "version",
          isVisible: true,
          isSortable: true,
          isGroupable: true,
          dataType: DataType.SHORT_TEXT,
        },
        {
          id: this.$nano.id(),
          name: "fileSize",
          label: "Size",
          isVisible: true,
          isSortable: true,
          isGroupable: false,
          dataType: DataType.SHORT_TEXT,
        },
      ],
      groupBy: "",
      filterBy: [],
      totalItems: 0,
      itemsPerPage: 10,
      currentPage: 1,
      hideFileSheet: false,
      userList: [],
      workspaceId: 0,
      workspaceList: [],
      itemId: 0,
      repositoryId: 0,
      repositoriesList: [],
      SignReportList: [],
      repositorySignReportList: [],
      repositorySignReportListData: [],
      options: {},
      selectedFile: {},
      isFileSheetVisible: false,
      loadingBarContent: false,
      fileStatus: [
        {
          id: this.$nano.id(),
          label: "Archived",
          value: "Archived",
        },
        {
          id: this.$nano.id(),
          label: "Deleted",
          value: "Deleted",
        },
      ],
      signType: [
        {
          id: this.$nano.id(),
          label: "e-sign",
          value: "esign",
        },
        {
          id: this.$nano.id(),
          label: "Digital Sign",
          value: "digitalSign",
        },
      ],
      exportDataList: false,
      historyProcessId: 0,
      selectedProcess: {},
    };
  },

  computed: {
    _columns() {
      let columns = [
        {
          id: this.$nano.id(),
          name: "fileName",
          label: "FileName",
          isVisible: false,
          isSortable: true,
          isGroupable: false,
          dataType: DataType.SHORT_TEXT,
        },
        {
          id: this.$nano.id(),
          name: "requestedAt",
          label: "Requested At",
          isVisible: true,
          isSortable: true,
          isGroupable: true,
          dataType: DataType.DATE,
        },
        {
          id: this.$nano.id(),
          name: "requestedBy",
          label: "Requested By",
          options: this.userList,
          isVisible: true,
          isSortable: true,
          isGroupable: false,
          dataType: DataType.SINGLE_SELECT,
        },
        {
          id: this.$nano.id(),
          name: "currentUser",
          label: "Current User",
          options: this.userList,
          isVisible: true,
          isSortable: true,
          isGroupable: false,
          dataType: DataType.SINGLE_SELECT,
        },
        {
          id: this.$nano.id(),
          name: "fileStatus",
          label: "Status",
          options: this.fileStatus,
          isVisible: true,
          isSortable: true,
          isGroupable: true,
          dataType: DataType.SINGLE_SELECT,
        },
        {
          id: this.$nano.id(),
          name: "signType",
          label: "Sign Type",
          options: this.signType,
          isVisible: true,
          isSortable: true,
          isGroupable: true,
          dataType: DataType.SINGLE_SELECT,
        },
      ];
      columns[2].options = this.userList;

      return columns;
    },

    _tabs() {
      if (this.$store.state.profileMenus.length) {
        this.tabs.forEach((tab) => {
          if (!tab.access) {
            tab.access = this.$store.state.profileMenus.find(
              (row) => row.folders
            )?.Menu[tab.accessLabel];
          }
        });
      }
      return this.tabs.filter((tab) => tab.access);
    },

    items() {
      return this.repositorySignReportList.map(({ key, value }) => ({
        key: "",
        value: key,
        data: value.map((item) => ({
          icon: item.status
            ? "eva-checkmark-circle-2-outline"
            : "eva-clock-outline",
          color: item.status ? "green" : "orange",
          iconTooltip: item.status ? "Completed" : "Processing",
          itemId: item.itemId,
          link: true,
          fileName: item.fileName,
          fileSize: item.fileSize ? formatBytes(item.fileSize) : "",
          fileStatus: item.status,
          signType: item.signType,
          requestedAt: this.$day.format(item.requestedAt),
          requestedBy: item.requestedByEmail,
          version: item.version,
          currentUser: item.currentUser,
          Stage: item.stageName,
          pId: item.processId,
          wId: item.workflowId,
        })),
      }));
    },
  },

  watch: {
    $route: {
      immediate: true,
      deep: true,
      handler() {
        this.tab = this.$route.name;
        const index = this.breadcrumbs.length - 1;
        this.breadcrumbs[index].label = this.$route.meta.breadcrumb;
      },
    },

    workspaceId: {
      deep: true,
      immediate: true,
      handler() {
        this.clearData();
        if (this.workspaceId) {
          this.getRepositories();
        }
        if (this.workspaceId && this.repositoryId) {
          this.getSignReport();
        }
      },
    },

    repositoryId: {
      deep: true,
      immediate: true,
      async handler() {
        this.repositorySignReportList = [];
        if (this.repositoryId) {
          this.getSignReport();
        }
      },
    },

    itemsPerPage: {
      deep: true,
      immediate: true,
      handler() {
        if (this.workspaceId && this.repositoryId && this.itemsPerPage) {
          this.getSignReport();
        }
      },
    },

    currentPage: {
      deep: true,
      immediate: true,
      handler() {
        if (this.workspaceId && this.repositoryId && this.currentPage) {
          this.getSignReport();
        }
      },
    },
  },
  created() {
    this.getWorkspaces();
    this.getUsers();
  },

  methods: {
    refreshData() {
      if (this.workspaceId && this.repositoryId) {
        this.getSignReport();
      } else if (this.workspaceId) {
        this.getRepositories();
      }
    },

    searchRow(search) {
      if (search) {
        this.repositorySignReportList = this.repositorySignReportListData.map(
          ({ key, value }) => ({
            key: key,
            value: value.filter((row) => {
              for (let cell in row) {
                let index = this._columns.findIndex((column) => {
                  return column.name === cell;
                });
                if (index >= 0 && row[cell]) {
                  if (
                    String(row[cell])
                      .toLowerCase()
                      .includes(search.toLowerCase())
                  ) {
                    return row;
                  }
                }
              }
            }),
          })
        );
      } else {
        this.repositorySignReportList = this.repositorySignReportListData;
      }
    },

    async export_Data() {
      this.$store.commit("showLoadingBarPage");
      const { error, payload } = await report.getSignReport(this.repositoryId, {
        filterBy: this.filterBy,
        itemsPerPage: 500,
        currentPage: 1,
      });
      this.$store.commit("hideLoadingBarPage");

      if (error) {
        this.$alert.error(error);
        return;
      }
      let tableData = [];
      payload.data.forEach((row) => {
        row.value.forEach((item) => {
          tableData.push({
            "File Name": item.fileName,
            Status: item.completedAt || "Processing",
            "Requested By": item.requestedByEmail,
            "Current User": item.currentUser,
            "Requested At": this.$day.format(item.requestedAt),
            Size: item.fileSize ? formatBytes(item.fileSize) : "",
            "Sign Type": item.signType,
            version: item.version,
            // "Stage":item.stageName,
          });
        });
      });

      if (tableData.length) {
        let selectedName = this.repositoriesList.find(
          (repository) => repository.value === this.repositoryId
        );
        exportData(tableData, selectedName.label + "-Signature Report");
      }
    },

    openFile(rowId) {
      let selectedList = this.repositorySignReportList[0].value.find(
        (file) => file.rNo === rowId
      );
      this.selectedFile = {
        id: selectedList.itemid,
        name: selectedList.fileName,
        size: selectedList.Filesize,
        repositoryId: this.repositoryId,
        createdAt: selectedList.createdAt,
        createdBy: "",
      };
      this.isFileSheetVisible = true;
    },

    /* api Functions */

    async getWorkspaces() {
      this.$store.commit("showLoadingBarPage");

      const { error, payload } = await workspace.getWorkspaceList();

      this.$store.commit("hideLoadingBarPage");

      if (error) {
        this.$alert.error(error);
        return;
      }

      if (!payload) {
        return;
      }

      this.workspaceList = payload.map((workspace) => ({
        id: this.$nano.id(),
        label: workspace.value,
        value: workspace.id,
      }));
    },

    async getRepositories() {
      if (this.workspaceId) {
        this.repositoriesList = [];
        // this.$store.commit("showLoadingBarPage");
        this.loadingBarContent = true;

        const { error, payload } = await repository.getRepositoryList(
          "workspaceId",
          this.workspaceId
        );

        this.loadingBarContent = false;
        // this.$store.commit("hideLoadingBarPage");

        if (error) {
          this.$alert.error(error);
          return;
        }
        if (!payload.length) {
          return;
        }
        this.repositoriesList = payload.map((repository) => ({
          id: this.$nano.id(),
          label: repository.value,
          value: repository.id,
        }));
      }
    },

    async getUsers() {
      this.userList = [];
      const { error, payload } = await user.getUserList();

      if (error) {
        this.$alert.error("Error fetching users");
        return;
      }

      if (payload) {
        payload.map((user) => {
          this.userList.push({
            id: this.$nano.id(),
            label: user.value,
            value: user.value,
          });
        });
      }
    },

    async getSignReport() {
      this.loadingBarContent = true;
      // this.$store.commit("showLoadingBarPage");
      this.repositorySignReportList = [];
      this.repositorySignReportListData = [];
      this.list = [];
      this.filterBy.forEach((group) => {
        if (group.filters.length) {
          group.filters.forEach((row) => {
            if (row.dataType === "SINGLE_SELECT") {
              row.value = JSON.stringify([row.arrayValue]);
            }
          });
        }
      });
      const { error, payload } = await report.getSignReport(this.repositoryId, {
        sortBy: this.sortBy,
        filterBy: this.filterBy,
        currentPage: this.currentPage,
        itemsPerPage: this.itemsPerPage,
      });
      // this.$store.commit("hideLoadingBarPage");
      this.loadingBarContent = false;

      if (error) {
        this.$alert.error(error);
        return;
      }

      this.totalItems = payload.meta?.totalItems || 0;

      if (this.totalItems === 0) {
        this.repositorySignReportList = [];
        this.repositorySignReportListData = [];
      } else {
        console.log(payload.data);
        this.repositorySignReportList = payload.data;
        this.repositorySignReportListData = payload.data;
        if (this.repositorySignReportList.length) {
          this.exportDataList = false;
          this.exportDataList = true;
        }
      }
    },

    async getData(fromDate, toDate) {
      const filterBy = [
        {
          id: this.$nano.id(),
          filters: [
            {
              id: this.$nano.id(),
              criteria: "requestedAt",
              condition: "IS_EQUALS_TO",
              value: fromDate,
              valueTo: toDate,
              dataType: "DataType.DATE",
            },
          ],
          groupCondition: "",
        },
      ];

      this.$store.commit("showLoadingBarPage");
      this.repositorySignReportList = [];
      this.repositorySignReportListData = [];
      this.list = [];
      const { error, payload } = await report.getSignReport(
        this.repositoryId,

        {
          sortBy: this.sortBy,
          filterBy: filterBy,
          itemsPerPage: this.itemsPerPage,
          currentPage: this.currentPage,
        }
      );
      this.$store.commit("hideLoadingBarPage");

      if (error) {
        this.$alert.error(error);
        return;
      }
      if (payload.data.length) {
        payload.data.forEach((row) => {
          row.value.forEach((process) => {
            this.SignReportList.push({
              ...process,
            });
          });
        });
      }

      this.totalItems = payload.meta?.totalItems || 0;

      if (this.totalItems === 0) {
        //this.$alert.info("No records found");
        this.repositorySignReportList = [];
        this.repositorySignReportListData = [];
      } else {
        this.repositorySignReportList = payload.data;
        this.repositorySignReportListData = payload.data;
      }
    },

    clearData() {
      this.exportDataList = false;
      this.repositoryId = 0;
      this.repositorySignReportList = [];
    },
    showHistory(item) {
      this.historyProcessId = item.pId;
      this.selectedProcess = {
        requestNo: item.fileName,
        id: item.wId,
      };
    },
    /* ... */
  },
};
</script>

<style lang="scss" scoped>
#repository-sign-reports {
  .content {
    padding-top: 8px;
    min-height: calc(100vh - 300px);
  }

  .backGround {
    border: 2px;
    border-radius: 10px;
    background-color: #87dcf626;
    margin-bottom: 8px;
  }
}
</style>

<style lang="scss">
#select-field.fieldList {
  .select {
    min-height: 0px !important;
    color: $secondary !important;
    width: 140px !important;
    overflow: hidden !important;
  }
}

#text-field.textField {
  input {
    min-height: 35px;
    width: 140px;
  }
}

.loading-content {
  background-color: var(--body-bg-color) !important;
  position: absolute;
  z-index: 9999;
}
</style>
